___CSS_LOADER_EXPORT___.push([module.id, `.chat-container {
    border: 1px solid;
    border-radius: 8px;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.chat-messages {
    flex: 1;
    overflow-y: auto;
    padding: 5px;
    display: flex;
    flex-direction: column;
}

.message-input-container {
    display: flex;
    margin-top: 10px;
    padding-top: 10px;
}

.message-input-field {
    flex: 1;
    padding: 8px;
    border: 1px solid;
    border-radius: 4px;
    margin-right: 5px;
}

.message-input-field:focus {
    outline: none;
}

.message-input-button {
    background-color: #007bff;
    color: white;
    padding: 8px 12px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.message-input-container .disabled {
    background-color: #efefef4d;
    color: #1010104d;
    border-color: #efefef4d;
    cursor: auto;
}

.message-input-button:hover {
    background-color: #0056b3;
}

.message-input-container .disabled:hover {
    background-color: #efefef4d;
}

.link-message {
    color: #fff;
    text-decoration: underline;
}

.empty-chat {
    margin: 0 auto;
}`, "",{"version":3,"sources":["webpack://./src/styles/chatStyles.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;IACb,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,OAAO;IACP,gBAAgB;IAChB,YAAY;IACZ,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,iBAAiB;AACrB;;AAEA;IACI,OAAO;IACP,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,2BAA2B;IAC3B,gBAAgB;IAChB,uBAAuB;IACvB,YAAY;AAChB;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,WAAW;IACX,0BAA0B;AAC9B;;AAEA;IACI,cAAc;AAClB","sourcesContent":[".chat-container {\n    border: 1px solid;\n    border-radius: 8px;\n    padding: 10px;\n    display: flex;\n    flex-direction: column;\n}\n\n.chat-messages {\n    flex: 1;\n    overflow-y: auto;\n    padding: 5px;\n    display: flex;\n    flex-direction: column;\n}\n\n.message-input-container {\n    display: flex;\n    margin-top: 10px;\n    padding-top: 10px;\n}\n\n.message-input-field {\n    flex: 1;\n    padding: 8px;\n    border: 1px solid;\n    border-radius: 4px;\n    margin-right: 5px;\n}\n\n.message-input-field:focus {\n    outline: none;\n}\n\n.message-input-button {\n    background-color: #007bff;\n    color: white;\n    padding: 8px 12px;\n    border: none;\n    border-radius: 4px;\n    cursor: pointer;\n}\n\n.message-input-container .disabled {\n    background-color: #efefef4d;\n    color: #1010104d;\n    border-color: #efefef4d;\n    cursor: auto;\n}\n\n.message-input-button:hover {\n    background-color: #0056b3;\n}\n\n.message-input-container .disabled:hover {\n    background-color: #efefef4d;\n}\n\n.link-message {\n    color: #fff;\n    text-decoration: underline;\n}\n\n.empty-chat {\n    margin: 0 auto;\n}"],"sourceRoot":""}]);
